import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import file from '@/components/File'
import aria2 from '@/components/Aria2'
import login from '@/components/Login'
import video from '@/components/Video.vue'
import onedrive from '@/components/OneDriveCallBack.vue'
import userSetting from '@/components/UserSetting.vue'
import userGroupSetting from '@/components/UserGroupSetting.vue'
import storeSetting from '@/components/StoreSetting.vue'
import aria2Setting from '@/components/Aria2Setting.vue'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'HelloWorld',
      component: HelloWorld,
      children:[
        {
          path: 'home',
          component: file
        },
        {
          path: 'aria2',
          component: aria2
        },
        {
          path: 'video',
          component: video
        },
        {
          path: 'userSetting',
          component: userSetting
        },
        {
          path: 'userGroupSetting',
          component: userGroupSetting
        },
        {
          path: 'storeSetting',
          component: storeSetting
        },
        {
          path: 'aria2Setting',
          component: aria2Setting
        }
      ]
    },
    {
      path: '/login',
      name: 'Login',
      component: login
    },
    {
      path: '/callback',
      component: onedrive
    }
  ]
})



