<template>
  <div class="mdui-container">
    <div class="mdui-table-fluid">
    <table class="mdui-table mdui-table-hoverable">
      <thead>
        <tr>
          <th>ID</th>
          <th>名称</th>
          <th>类型</th>
          <th>已用</th>
          <th>容量</th>
          <th>操作</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(item,i) in tableData" :key="i">
          <td>{{item.ID}}</td>
          <td>{{item.Name}}</td>
          <td>{{item.Type}}</td>
          <td>{{size_(item.Used)}}</td>
          <td>{{size_(item.Volume)}}</td>
          <td>
            <button class="mdui-btn mdui-btn-icon mdui-ripple"><i class="mdui-icon material-icons">edit</i></button>
          </td>
        </tr>
      </tbody>
    </table>
    </div>
    <div class="mdui-dialog" id="mdui-dialog">
      <div class="mdui-dialog-title">添加储存策略</div>
      <div class="mdui-dialog-content">
        <p><a href="https://portal.azure.com/#blade/Microsoft_AAD_IAM/ActiveDirectoryMenuBlade/Overview" target="view_window">储存方式：OneDrive</a>重定向地址：{{url}}</p>
        <div class="mdui-textfield mdui-textfield-floating-label">
          <label class="mdui-textfield-label">储存名称</label>
          <textarea v-model="name" class="mdui-textfield-input"></textarea>
        </div>
        <div>
          <div class="mdui-textfield mdui-textfield-floating-label">
          <label class="mdui-textfield-label">容量(KB)</label>
          <textarea v-model="volume" class="mdui-textfield-input"></textarea>
          </div>
        </div>
        <div class="mdui-textfield mdui-textfield-floating-label">
          <label v-model="path_" class="mdui-textfield-label">存储路径</label>
          <textarea class="mdui-textfield-input"></textarea>
        </div>
        <div class="mdui-textfield mdui-textfield-floating-label">
          <label class="mdui-textfield-label">应用程序ID</label>
          <textarea v-model="client_id" class="mdui-textfield-input"></textarea>
        </div>
        <div class="mdui-textfield mdui-textfield-floating-label">
          <label class="mdui-textfield-label">客户端密码</label>
          <textarea v-model="client_secret" class="mdui-textfield-input"></textarea>
        </div>
      </div>
      <div class="mdui-dialog-actions">
        <button class="mdui-btn mdui-ripple" @click="addstore" mdui-dialog-confirm>添加</button>
        <button class="mdui-btn mdui-ripple" mdui-dialog-cancel>取消</button>
      </div>
    </div>
    <button mdui-dialog="{target:'#mdui-dialog'}" class="mdui-fab mdui-fab-fixed mdui-ripple mdui-color-blue-200"><i class="mdui-icon material-icons">add</i></button>
  </div>
</template>

<script>
  export default{
    data () {
      return {
        tableData: [],
        dialogVisible: false,
        type: 'onedrive',
        input: '',
        select: '0',
        name: '',
        volume: '',
        path_: '',
        client_id: '',
        client_secret: '',
        help: false,
        url: ''
      }
    },
    methods:{
      addstore: function () {
        var url
        url = `https://login.microsoftonline.com/common/oauth2/v2.0/authorize?client_id=`+this.client_id+`&scope=files.readwrite.all+offline_access
      &response_type=code&redirect_uri=` + this.url
        this.$cookies.set('name',this.name)
        this.$cookies.set('path_',this.path_)
        this.$cookies.set('volume',this.volume)
        this.$cookies.set('client_secret',this.client_secret)
        this.$cookies.set('client_id',this.client_id)
        this.$cookies.set('url',this.url)
        window.open(url)
      },
      size_: function (values) {
        var unit = ['KB', 'MB', 'GB', 'TB']
        var i = 0
        let value = parseFloat(values)
        while (value >= 1024) {
          value = value / 1024
          i++
        }
        let tmp = value.toFixed(2) + unit[i]
        return (tmp)
      }
    },
    beforeMount () {
      this.url = 'https://' + document.domain + '/callback'
      this.$axios.get('/store').then(res=>{
        if (res.status == 400) {
          this.$message.error("获取失败")
          return
        }
        this.tableData = res.data
      })
    }
  }
</script>

<style>
</style>



