<template>
  <div class="hello">
  <div class="mdui-appbar mdui-appbar-fixed">
    <div class="mdui-toolbar mdui-color-blue-200">
      <a class="mdui-btn mdui-btn-icon" mdui-drawer="{target: '#drawer'}">
        <i class="mdui-icon material-icons">menu</i>
      </a>
      <a href="javascript:;" @click="gohome" class="mdui-typo-headline">GoPan</a>
      <div class="mdui-toolbar-spacer"></div>
      <div class="mdui-textfield mdui-textfield-expandable mdui-float-right" style="max-width: 250px;">
        <button class="mdui-textfield-icon mdui-btn mdui-btn-icon"><i class="mdui-icon material-icons">search</i></button>
        <input class="mdui-textfield-input" type="text" placeholder="Search"/>
        <button class="mdui-textfield-close mdui-btn mdui-btn-icon"><i class="mdui-icon material-icons">close</i></button>
      </div>
      <a href="javascript:;" class="mdui-btn mdui-btn-icon mdui-valign" mdui-menu="{target: '#menu',covered: false}">
        <div class="mdui-list-item-avatar mdui-center">
          <img src="https://gimg2.baidu.com/image_search/src=http%3A%2F%2Fb-ssl.duitang.com%2Fuploads%2Fitem%2F201705%2F13%2F20170513155641_wCyQ2.jpeg&refer=http%3A%2F%2Fb-ssl.duitang.com&app=2002&size=f9999,10000&q=a80&n=0&g=0n&fmt=jpeg?sec=1618239261&t=1f02d7bbac80574b85ecb9e2031dbe32"/>
        </div>
      </a>
      <ul class="mdui-menu" id = "menu" style="border-radius: 10px;">
        <li class="mdui-list-item mdui-ripple" @click="goAdminOrFile">
          <i class="mdui-list-item-icon mdui-icon material-icons">settings</i>
          <div class="mdui-list-item-content">{{user?"后台设置":"网盘首页"}}</div>
        </li>
        <li class="mdui-list-item mdui-ripple">
          <i class="mdui-list-item-icon mdui-icon material-icons">person</i>
          <div class="mdui-list-item-content">个人设置</div>
        </li>
        <li class="mdui-list-item mdui-ripple"@click="logout()">
          <i class="mdui-list-item-icon mdui-icon material-icons">power_settings_new</i>
          <div class="mdui-list-item-content">注销登录</div>
        </li>
      </ul>
    </div>
  </div>
  <div class="mdui-drawer" id="drawer" v-if="user">
    <ul class="mdui-list">
      <li class="mdui-list-item mdui-ripple" style="border-radius: 8px;" @click="goroute(0)">
        <i class="mdui-list-item-icon mdui-icon material-icons">folder</i>
        <div class="mdui-list-item-content">文件管理</div>
      </li>
      <li class="mdui-list-item mdui-ripple" style="border-radius: 8px;" v-if="aria2view" @click="goroute(1)">
        <i class="mdui-list-item-icon mdui-icon material-icons">cloud_down</i>
        <div class="mdui-list-item-content">离线下载</div>
      </li>
      <li class="mdui-list-item mdui-ripple"style="border-radius: 8px;">
        <i class="mdui-list-item-icon mdui-icon material-icons">share</i>
        <div class="mdui-list-item-content">我的分享</div>
      </li>
      <li class="mdui-list-item mdui-ripple"style="border-radius: 8px;">
        <i class="mdui-list-item-icon mdui-icon material-icons">assignment</i>
        <div class="mdui-list-item-content">任务队列</div>
      </li>
      <li class="mdui-list-item mdui-ripple"style="border-radius: 8px;position: absolute;bottom: 0px ;width: 100%;">
        <i class="mdui-list-item-icon mdui-icon material-icons">battery_full</i>
        <div class="mdui-list-item-content">容量
        <div class="mdui-progress">
          <div class="mdui-progress-determinate" :style="{'width': used/volume * 100 +'%'}"></div>
        </div>
        <div>{{size(used)}}/{{size(volume)}}</div>
        </div>
      </li>
    </ul>
  </div>
  <div class="mdui-drawer" id="drawer" v-if="!user">
    <ul class="mdui-list">
      <li class="mdui-list-item mdui-ripple" style="border-radius: 8px;" @click="goadminroute(0)">
        <i class="mdui-list-item-icon mdui-icon material-icons">folder</i>
        <div class="mdui-list-item-content">用户管理</div>
      </li>
      <li class="mdui-list-item mdui-ripple" style="border-radius: 8px;" @click="goadminroute(1)">
        <i class="mdui-list-item-icon mdui-icon material-icons">folder</i>
        <div class="mdui-list-item-content">用户组管理</div>
      </li>
      <li class="mdui-list-item mdui-ripple" style="border-radius: 8px;" @click="goadminroute(2)">
        <i class="mdui-list-item-icon mdui-icon material-icons">folder</i>
        <div class="mdui-list-item-content">储存设置</div>
      </li>
      <li class="mdui-list-item mdui-ripple" style="border-radius: 8px;" @click="goadminroute(3)">
        <i class="mdui-list-item-icon mdui-icon material-icons">folder</i>
        <div class="mdui-list-item-content">Aria2设置</div>
      </li>
    </ul>
  </div>
  <router-view></router-view>
  </div>
</template>

<script>
import file from './File.vue'
import aria2 from './Aria2.vue'
import userSetting from './UserSetting.vue'
import userGroupSetting from './UserGroupSetting.vue'
import storeSetting from './StoreSetting.vue'
import aria2Setting from './Aria2Setting.vue'
export default {
  name: 'HelloWorld',
  data () {
    return {
      rt: ['/home','/aria2'],
      adminrt: ['/userSetting','userGroupSetting','/storeSetting','/aria2Setting'],
      used: 0,
      volume: 0.001,
      aria2view: false,
      user: true
    }
  },
  methods:{
    goroute(num){
      if(this.$route.path != this.rt[num]){
        this.$router.push({path: this.rt[num]})
      }
    },
    goadminroute(num){
      if(this.$route.path != this.adminrt[num]){
        this.$router.push({path: this.adminrt[num]})
      }
    },
    gohome(){
      window.location.href= '/home'
    },
    size (value) {
      var unit = ['KB', 'MB', 'GB', 'TB']
      var i = 0
      while (value >= 1024) {
        value = value / 1024
        i++
      }
      return (value.toFixed(2) + unit[i])
    },
    logout: function () {
      this.$cookies.remove('GODKEY')
      mdui.snackbar({
          message: '注销成功',
          position: 'top'
        })
      this.$router.push({path: '/login'})
    },
    goAdminOrFile(){
      this.user = !this.user
      if(this.user){
        this.$router.push({path: '/home?path=%2F'})
      }else{
        this.$router.push({path: '/userSetting'})
      }

    }
  },
  components:{
    file,
    aria2,
    userSetting,
    userGroupSetting,
    storeSetting,
    aria2Setting
  },
  beforeCreate() {
    if (this.$cookies.get('GODKEY') == null) {
      mdui.snackbar({
          message: '未登录',
          position: 'top'
        })
      this.$router.push({path: '/login'})
    }
  },
  beforeMount() {
    if (this.$route.path === '/') {
      this.$router.push({path: '/home'})
    }
    this.$axios({
      method: 'POST',
      url: '/cookies'
    }).then(
      response => {
        if (response.data !== 'failed') {
          this.volume = response.data['Volume']
          this.used = response.data['Used']
        }
      }
    )
    this.$axios.get('/aria2test').then(
      res=>{
        if(res.data['Version'] != null){
          this.aria2view = true
        }
      }
    )
  }
}
</script>



