<template>
  <div>
    <div class="mdui-toolbar mdui-color-grey-200">
      <div v-for="(path,index) in route" :key="index" >
        <button class="mdui-btn mdui-color-grey-300 mdui-ripple mdui-text-truncate" @click="go_route(index)" style="border-radius: 10px;width: 30px;">{{path}}</button>
        <i class="mdui-icon material-icons">chevron_right</i>
      </div>

      <div class="mdui-toolbar-spacer"></div>
      <a href="javascript:;" class="mdui-btn mdui-btn-icon" @click="refresh" mdui-tooltip="{content: '刷新'}"><i class="mdui-icon material-icons">refresh</i></a>
      <a href="javascript:;" class="mdui-btn mdui-btn-icon" @click="AllCheck" v-if="alledit" mdui-tooltip="{content: '全选'}"><i class="mdui-icon material-icons">done_all</i></a>
      <a href="javascript:;" class="mdui-btn mdui-btn-icon" @click="" v-if="alledit" mdui-tooltip="{content: '分享'}"><i class="mdui-icon material-icons">share</i></a>
      <a href="javascript:;" class="mdui-btn mdui-btn-icon" @click="" v-if="alledit" mdui-tooltip="{content: '移动'}"><i class="mdui-icon material-icons">call_made</i></a>
      <a href="javascript:;" class="mdui-btn mdui-btn-icon" @click="" v-if="alledit" mdui-tooltip="{content: '下载'}"><i class="mdui-icon material-icons">cloud_download</i></a>
      <a href="javascript:;" class="mdui-btn mdui-btn-icon" @click="" v-if="alledit" mdui-tooltip="{content: '删除'}"><i class="mdui-icon material-icons">delete</i></a>
      <a href="javascript:;" class="mdui-btn mdui-btn-icon" @click="alledit=!alledit" mdui-tooltip="{content: '编辑'}"><i class="mdui-icon material-icons">edit</i></a>
    </div>
    <div class="neibuhua" style="padding: 8px;" @click="MenuView = false">
      <div v-show="dir_view">
        <p style="font-weight: 500;margin-left: 20px;margin-bottom: 5px;">文件夹</p>
        <div class="mdui-row">
          <div class="mdui-col-xs-6 mdui-col-sm-3 mdui-col-md-3 mdui-col-lg-2" style="border-radius: 10px;margin-top: 10px;margin-bottom: 10px;" v-for="(item,i) in dirlist" :key="i">
            <div class="mdui-hoverable" style="border-radius: 10px;height: 50px;">
              <label class="mdui-ripple">
                <div class="mdui-checkbox" style="position: absolute;top: -8px;left: 5px;" v-if="alledit">
                  <input type="checkbox" v-model="dircheck[i]" @click="chooseitem(dircheck[i])"/>
                  <i class="mdui-checkbox-icon"></i>
                </div>
                <button class="mdui-btn mdui-btn-raised mdui-ripple mdui-text-truncate" @contextmenu.stop.prevent="leftclick($event,item)" @click="enterdir(item['DataName'])" style="height: 100%;width: 100%;border-radius: 10px;padding-left: 30px;">{{item.DataName}}</button>
              </label>
            </div>
          </div>
        </div>
      </div>
      <div style="margin-top: 10px;" v-show="file_view">
        <p style="font-weight: 500;margin-left: 20px;margin-bottom: 5px;">文件</p>
        <div class="mdui-row">
          <div class="mdui-col-xs-6 mdui-col-sm-3 mdui-col-md-3 mdui-col-lg-2" style="border-radius: 10px;margin-top: 10px;margin-bottom: 10px;" v-for="(item,i) in filelist" :key="i">
            <div class="mdui-hoverable mdui-card" @contextmenu.stop.prevent="leftclick($event,item)" style="padding-top: 25px;border-radius: 10px;height: 205px;" :mdui-tooltip="`{content: '日期：`+ getLocalTime(item.DataTime)+`'}`">
              <label class="mdui-ripple">
                <div class="mdui-checkbox" style="position: absolute;top: -30px;left: 5px;" v-if="alledit">
                  <input type="checkbox" v-model="filecheck[i]" @click="chooseitem(filecheck[i])"/>
                  <i class="mdui-checkbox-icon"></i>
                </div>
                <div class="mdui-card-media" style="margin: 0px 20px 0px 25px;">
                  <img class="mdui-img-fluid" preview="0" v-if="filetype(item.DataName) == 'picture'" style="border-radius: 8px;height: 135px;" @click="photoView" :src="tubiao(item)" />
                  <img class="mdui-img-fluid" v-if="filetype(item.DataName) != 'picture'" style="border-radius: 8px;height: 135px;" :src="tubiao(item)" />
                </div>
                  <p class="mdui-text-truncate" style="text-align: center;margin-left: 15%;margin-right: 15%;">{{item.DataName}}</p>
              </label>
            </div>
          </div>
        </div>
      </div>
      <ul class="mdui-list mdui-color-grey-50 mdui-shadow-8" v-show="MenuView" :style="{top:top + 'px',left:left + 'px',position:'fixed','border-radius':'10px'}">
        <li class="mdui-list-item mdui-ripple" v-if="filetype(focusItem.Name) == 'video'" @click="playvideo">
          <i class="mdui-list-item-icon mdui-icon material-icons">play_arrow</i>
          <div class="mdui-list-item-content">播放</div>
        </li>
        <li class="mdui-list-item mdui-ripple" v-if="filetype(focusItem.Name) == 'compress'" mdui-dialog= "{target:'#archivedialog'}" @click="moveButton1">
          <i class="mdui-list-item-icon mdui-icon material-icons">archive</i>
          <div class="mdui-list-item-content">解压</div>
        </li>
        <li class="mdui-list-item mdui-ripple" @click="filedown">
          <i class="mdui-list-item-icon mdui-icon material-icons">move_to_inbox</i>
          <div class="mdui-list-item-content">下载</div>
        </li>
        <li class="mdui-list-item mdui-ripple" mdui-dialog= "{target:'#movedialog'}" @click="moveButton1">
          <i class="mdui-list-item-icon mdui-icon material-icons">send</i>
          <div class="mdui-list-item-content">移动</div>
        </li>
        <li class="mdui-list-item mdui-ripple" mdui-dialog="{target:'#renamedialog'}" @click="namecopy">
          <i class="mdui-list-item-icon mdui-icon material-icons">delete</i>
          <div class="mdui-list-item-content">重命名</div>
        </li>
        <li class="mdui-list-item mdui-ripple" @click="deletefile()">
          <i class="mdui-list-item-icon mdui-icon material-icons">border_color</i>
          <div class="mdui-list-item-content">删除</div>
        </li>
      </ul>
      <div class="mdui-fab-wrapper" id="fab" mdui-fab="{trigger: 'hover'}">
        <button class="mdui-fab mdui-ripple mdui-color-pink-accent">
          <i class="mdui-icon material-icons">add</i>
          <i class="mdui-icon mdui-fab-opened material-icons">arrow_upward</i>
        </button>
        <div class="mdui-fab-dial">
          <button class="mdui-fab mdui-fab-mini mdui-ripple mdui-color-pink" mdui-tooltip="{content: '本地上传',position: 'left'}"><i class="mdui-icon material-icons">backup</i>
          </button>
          </button>
          <button class="mdui-fab mdui-fab-mini mdui-ripple mdui-color-orange" mdui-tooltip="{content: '新建文件',position: 'left'}"><i class="mdui-icon material-icons">note_add</i>
          </button>
          <button class="mdui-fab mdui-fab-mini mdui-ripple mdui-color-blue" mdui-dialog= "{target:'#newdirdialog'}" mdui-tooltip="{content: '新建文件夹',position: 'left'}"><i class="mdui-icon material-icons">create_new_folder</i>
          </button>
        </div>
      </div>
      <div class="mdui-dialog" id="renamedialog" style="width: 350px;border-radius: 10px;">
          <div class="mdui-dialog-title">重命名</div>
            <div class="mdui-dialog-content mdui-center" style="width: 300px;">
              <div class="mdui-textfield">
                <input v-model="rename" class="mdui-textfield-input" type="text" placeholder="文件名"/>
              </div>
            </div>
          <div class="mdui-dialog-actions">
            <button class="mdui-btn mdui-ripple" @click="renameitem" mdui-dialog-confirm>修改</button>
            <button class="mdui-btn mdui-ripple mdui-dialog-cancel" mdui-dialog-cancel>取消</button>
          </div>
      </div>
      <div class="mdui-dialog" id="newdirdialog" style="width: 350px;border-radius: 10px;">
          <div class="mdui-dialog-title">新建文件夹</div>
            <div class="mdui-dialog-content mdui-center" style="width: 300px;">
              <div class="mdui-textfield">
                <input v-model="newdirname" class="mdui-textfield-input" type="text" placeholder="文件夹名"/>
              </div>
            </div>
          <div class="mdui-dialog-actions">
            <button class="mdui-btn mdui-ripple" @click="createdir" mdui-dialog-confirm>创建</button>
            <button class="mdui-btn mdui-ripple mdui-dialog-cancel" mdui-dialog-cancel>取消</button>
          </div>
      </div>
      <div class="mdui-dialog" id="movedialog" style="border-radius: 10px;">
          <div class="mdui-dialog-title mdui-text-truncate">移动至 {{movepath}}</div>
          <div class="mdui-dialog-content mdui-center" style="min-height: 300px;">
            <div class="mdui-row">
              <div class="mdui-col-xs-6 mdui-col-md-4" v-for="(item,i) in movedirlist" :key="i">
                <button style="margin: 5px;width: 150px;" class="mdui-btn mdui-color-grey-200 mdui-ripple mdui-text-truncate" @click="moveButton2(item)">{{item}}</button>
              </div>
            </div>
          </div>
          <div class="mdui-dialog-actions">
            <button class="mdui-btn mdui-ripple" @click="moveItem" mdui-dialog-confirm>移动</button>
            <button class="mdui-btn mdui-ripple mdui-dialog-cancel" mdui-dialog-cancel>取消</button>
          </div>
      </div>
      <div class="mdui-dialog" id="archivedialog" style="border-radius: 10px;">
          <div class="mdui-dialog-title mdui-text-truncate">解压至 {{movepath}}</div>
          <div class="mdui-dialog-content mdui-center" style="min-height: 300px;">
            <div class="mdui-row">
              <div class="mdui-col-xs-6 mdui-col-md-4" v-for="(item,i) in movedirlist" :key="i">
                <button style="margin: 5px;width: 150px;" class="mdui-btn mdui-color-grey-200 mdui-ripple mdui-text-truncate" @click="moveButton2(item)">{{item}}</button>
              </div>
            </div>
          </div>
          <div class="mdui-dialog-actions">
            <div class="mdui-textfield">
              <input v-model="password" class="mdui-textfield-input" type="text" placeholder="RAR解压密码"/>
            </div>
            <button class="mdui-btn mdui-ripple" @click="archive" mdui-dialog-confirm>解压</button>
            <button class="mdui-btn mdui-ripple mdui-dialog-cancel" mdui-dialog-cancel>取消</button>
          </div>
      </div>
    </div>
  </div>
</template>

<script>
const video = ['wav', 'avi', 'mov', 'mp4', 'flv', 'rmvb', 'mpeg', 'mpg', 'mkv']
const compress = ['rar', 'zip', '7z', 'gz', 'tar', 'gz']
const picture = ['jpg', 'jpeg', 'bmp', 'gif', 'png', 'tif']
const sound = ['mp3', 'wma', 'mmf', 'flac', 'acc', 'amr']
export default{
  data(){
    return{
      list: [],
      alledit: false,
      dircheck: [],
      filecheck: [],
      leftmenu: '',
      top: 0,
      left: 0,
      MenuView: false,
      route: [],
      alldatalist: [],
      dir_view: false,
      file_view: false,
      dirlist: [],
      filelist: [],
      focusItem:{
        Name: '',
        ID: '',
        Path: '',
        Type: ''
      },
      rename:'',
      imgs: {
        video: require('../assets/文件类型 - 视频文件.png'),
        compress: require('../assets/文件类型 - 压缩文件.png'),
        music: require('../assets/文件类型 - 音乐.png'),
        word: require('../assets/文件类型 - word.png'),
        ppt: require('../assets/文件类型 - ppt.png'),
        pdf: require('../assets/文件类型 - PDF.png'),
        excel: require('../assets/文件类型 - excel.png'),
        none: require('../assets/文件类型 - 未识别文件占位图.png'),
        text: require('../assets/文件类型 - 文本文档.png'),
        exe: require('../assets/文件类型 - exe执行文件.png')
      },
      movepath:'',
      movedirlist: [],
      password:'',
      newdirname:''
    }
  },
  watch: {
    $route (to, from) {
      if(to.hash == "" && from.hash == ""){
        this.refresh()
      }
    }
  },
  methods:{
    AllCheck(){
      for(let i = 0; i < this.dirlist.length; i++){
        this.dircheck[i] = !this.dircheck[i]
      }
      for(let i = 0; i < this.filelist.length; i++){
        this.filecheck[i] = !this.filecheck[i]
      }
    },
    photoView(){
      this.$previewRefresh()
    },
    createdir: function () {
        let tmp
        if (this.route.length > 1) {
          tmp = this.pathencode()
        } else {
          tmp = '/'
        }
        let params = new FormData()
        params.append('path', tmp)
        params.append('name', this.newdirname)
        this.$axios({
          url: '/dir',
          method: 'post',
          data: params,
          headers: {
            'Content-Type': 'multipart/form-data'
          }
        }).then(response => {
          this.dialogVisible = false
          this.refresh()
          if (response.data === 'succeed') {
            mdui.snackbar({
                message: '创建文件夹成功',
                position: 'top'
              })
          } else {
            mdui.snackbar({
                message: '创建文件夹成功',
                position: 'top'
            })
          }
        })
    },
    archive(){
      let tmp = {
        FileID: this.focusItem.ID,
        NewPath: this.movepath,
        PassWord: this.PassWord
      }
      this.$axios({
        method: 'PUT',
        url: '/UnArchive',
        data: tmp
      }).then(res => {
        if (res.data === 'succeed') {
          mdui.snackbar({
              message: '解压任务提交成功',
              position: 'top'
            })
        } else {
          mdui.snackbar({
              message: '解压任务提交失败',
              position: 'top'
            })
        }
      })
    },
    playvideo(){
      this.$router.push({path: 'video?ID=' + this.focusItem.ID})
    },
    filetype(name){
      let index = name.lastIndexOf('.')
      let extend = name.substr(index + 1).toLowerCase()
      if (index !== -1) {
        if (video.indexOf(extend) > -1) {
          return "video"
        } else if (compress.indexOf(extend) > -1) {
          return "compress"
        } else if (sound.indexOf(extend) > -1) {
          return "music"
        } else if (extend === 'doc' || extend === 'docx') {
          return null
        } else if (extend === 'pdf') {
          return null
        } else if (extend === 'ppt' || extend === 'pptx') {
          return null
        } else if (extend === 'xls' || extend === 'xlsx') {
          return null
        } else if (extend === 'exe') {
          return null
        } else if (extend === 'txt') {
          return "txt"
        }else if (picture.indexOf(extend) > -1){
          return "picture"
        }else {
          return null
        }
      } else {
        return null
      }
    },
    chooseitem(i){
      console.log(i)
    },
    filedown() {
      window.location.href = '/api/filedown/' + this.focusItem.ID
    },
    moveItem(){
      let tmp = {
        FileID: this.focusItem.ID,
        FileName: this.focusItem.Name,
        OldPath: this.focusItem.Path,
        NewPath: this.movepath,
        Type: this.focusItem.Type
      }
      this.$axios({
        method: 'PUT',
        url: '/move/',
        data: tmp
      }).then(res => {
        if (res.data === 'succeed') {
          mdui.snackbar({
              message: '移动成功',
              position: 'top'
            })
        } else {
          mdui.snackbar({
              message: '移动失败',
              position: 'top'
            })
        }
        this.refresh()
      })
    },
    moveButton1(){
      this.movedirlist = []
      this.movepath = '/'
      let par = encodeURIComponent(this.movepath)
      this.$axios.get('/file/' + par).then(
        response => {
          if (response.data !== null) {
            this.alldatalist = response.data
            for (var item of this.alldatalist) {
              if (item['DataType'] === 'dir') {
                this.movedirlist.push(item.DataName)
              }
            }
          }
        }
      )
    },
    moveButton2(name){
      this.movedirlist = []
      if (this.movepath == '/'){
        this.movepath += name
      }else{
        this.movepath += '/'+name
      }
      let par = encodeURIComponent(this.movepath)
      this.$axios.get('/file/' + par).then(
        response => {
          if (response.data !== null) {
            this.alldatalist = response.data
            for (var item of this.alldatalist) {
              if (item['DataType'] === 'dir') {
                this.movedirlist.push(item.DataName)
              }
            }
          }
        }
      )
    },
    renameitem(){
      if(this.rename == this.focusItem.Name){
        mdui.snackbar({
            message: '未修改',
            position: 'top'
          })
        return
      }
      let tmp = {
        OldName: this.focusItem.Name,
        NewName: this.rename,
        Path: this.focusItem.Path,
        FileID: this.focusItem.ID,
        Type: this.focusItem.Type
      }
      this.$axios({
        method: 'PUT',
        url: '/file',
        data: tmp
      }).then((res) => {
        if (res.data === 'succeed') {
          mdui.snackbar({
              message: '重命名成功',
              position: 'top'
            })
        } else {
          mdui.snackbar({
              message: '重命名失败',
              position: 'top'
            })
        }
        this.refresh()
      })
    },
    leftclick(v,item){
      this.top = v.y
      this.left = v.x
      this.focusItem.ID = item.DataFileId
      this.focusItem.Name = item.DataName
      this.focusItem.Path = item.DataPath
      this.focusItem.Type = item.DataType
      this.MenuView = true
    },
    mainright(){
      this.MenuView = false
    },
    refresh(){
      this.dir_view = false
      this.file_view  = false
      this.dirlist = []
      this.filelist = []
      let nowpath = this.$route.query.path
      var par = encodeURIComponent(nowpath)
      if (nowpath.length > 1) {
        this.route = nowpath.split('/')
      }
      this.route[0] = '/'
      this.$axios.get('/file/' + par).then(
        response => {
          if (response.data !== null) {
            this.alldatalist = response.data
            for (var item of this.alldatalist) {
              if (item['DataType'] === 'dir') {
                this.dir_view = true
                this.dirlist.push(item)
              } else {
                this.file_view = true
                this.filelist.push(item)
              }
            }
          }
        }
      )
    },
    getLocalTime: function (nS) {
      return new Date(nS * 1000).toLocaleString().replace(/:\d{1,2}$/, ' ')
    },
    enterdir: function (path) {
      this.route.push(path)
      this.$router.push('home?path=' + this.pathencode())
    },
    pathencode: function () {
      let url = ''
      for (let j = 1; j < this.route.length; j++) {
        url += '%2F' + this.route[j]
      }
      return url
    },
    go_route: function (num) {
      if (this.route.length - 1 === num) {
        this.refresh()
        return
      }
      this.loading = true
      if (num > 0) {
        this.route = this.route.slice(0, num + 1)
        this.$router.push('home?path=' + this.pathencode())
      } else {
        this.route = ['/']
        this.$router.push('home?path=/')
      }
    },
    tubiao(item){
      let name = item.DataName
      let index = name.lastIndexOf('.')
      let extend = name.substr(index + 1).toLowerCase()
      if (index !== -1) {
        if (video.indexOf(extend) > -1) {
          return this.imgs.video
        } else if (compress.indexOf(extend) > -1) {
          return this.imgs.compress
        } else if (sound.indexOf(extend) > -1) {
          return this.imgs.music
        } else if (extend === 'doc' || extend === 'docx') {
          return this.imgs.word
        } else if (extend === 'pdf') {
          return this.imgs.pdf
        } else if (extend === 'ppt' || extend === 'pptx') {
          return this.imgs.ppt
        } else if (extend === 'xls' || extend === 'xlsx') {
          return this.imgs.excel
        } else if (extend === 'exe') {
          return this.imgs.exe
        } else if (extend === 'txt') {
          return this.imgs.text
        } else if (picture.indexOf(extend) > -1){
          return ('/api/thumbnail/' + item.DataFileId)
        } else{
          return this.imgs.none
        }
      } else {
        return this.imgs.none
      }
    },
    deletefile(){
      var url_
      if (this.focusItem.Type === 'file') {
        url_ = '/files/' + this.focusItem.ID
      } else {
        url_ = '/dir/' + this.focusItem.ID
      }
      this.$axios({
        method: 'DELETE',
        url: url_
      }).then((res) => {
        if (res.data === 'succeed') {
          mdui.snackbar({
              message: '删除成功',
              position: 'top'
            })
        } else {
          mdui.snackbar({
              message: '删除失败',
              position: 'top'
            })
        }
        this.refresh()
        this.focusItem = {
          ID: '',
          Path: '',
          Name: '',
          Type: ''
        }
      })
    },
    namecopy(){
      this.rename = this.focusItem.Name
    }
  },
  beforeMount() {
    document.title = '文件管理'
    let nowpath = this.$route.query.path
    if (nowpath == null) {
      this.$router.push('home?path=/')
    }else{
      this.refresh()
    }
  },
  mounted() {
    this.$previewRefresh()
  }
}
</script>

<style>
.mdui-checkbox input[type='checkbox']:focus:checked:not(:disabled) + .mdui-checkbox-icon {
    -webkit-box-shadow: none !important;
    box-shadow: none !important;
}

.mdui-checkbox input[type='checkbox']:focus + .mdui-checkbox-icon {
    -webkit-box-shadow: none !important;
    box-shadow: none !important;
}
.neibuhua{
  overflow-y:auto;
  height: calc(100vh - 140px);
}
.neibuhua::-webkit-scrollbar{
  width: 8px;
}
.neibuhua::-webkit-scrollbar-thumb{
  background-color: #BDBDBD;
  border-radius: 3px;
}
</style>



