<template>
  <div>
    <div>
      <h1>下载中<button class="mdui-btn mdui-btn-icon" @click="refresh"><i class="mdui-icon material-icons">autorenew</i></button></h1>
      <div class="mdui-panel" mdui-panel>
        <div class="mdui-panel-item" style="margin: 15px 20px 15px 20px;border-radius: 10px;" v-for="(item,i) in aria2process" v-if="completeOrDownload(item.Status)" :key='i'>
            <div class="mdui-panel-item-header" style="height: 100px;">
              <div class="mdui-panel-item-title">{{item.Name}}</div>
              <div class="mdui-panel-item-summary">
                <div class="mdui-progress">
                  <div class="mdui-progress-determinate" :style="{'width': getpercentnge(item.CompletedLength,item.TotalLength)+'%'}"></div>
                </div>
                <div class="mdui-row">
                  <div class="mdui-col-xs-12 mdui-col-sm-3" style="margin-top: 10px;">
                    进度：{{getpercentnge(item.CompletedLength,item.TotalLength)+'%'}}
                  </div>
                  <div class="mdui-col-xs-12 mdui-col-sm-6" style="margin-top: 10px;">
                  大小：{{size(item.CompletedLength)+'/'+size(item.TotalLength)}}
                  </div>
                  <div class="mdui-col-xs-12 mdui-col-sm-3" style="margin-top: 10px;">
                    速度：{{downspeed(item.DownloadSpeed)}}
                  </div>
                </div>
              </div>
              <i class="mdui-panel-item-arrow mdui-icon material-icons">keyboard_arrow_down</i>
            </div>
            <div class="mdui-panel-item-body">
              <div v-for="(item_,index_) in item.Files" :key="index_" style="margin-top: 15px;margin-bottom: 15px;">
                {{item_.path}}
                <div class="mdui-progress" style="margin-top: 8px;">
                  <div class="mdui-progress-determinate" :style="{'width': getpercentnge(item_.completedLength,item_.length)+'%'}"></div>
                </div>
              </div>
              <div class="mdui-panel-item-actions">
                <button class="mdui-btn mdui-ripple" style="border-radius: 10px;" @click="taskprocess(item)">
                {{
                  item.Status == 'active'?'暂停':'继续'
                  }}
                </button>
                <button class="mdui-btn mdui-ripple" style="border-radius: 10px;" @click="taskcancel(item)">取消</button>
              </div>
            </div>
        </div>
      </div>
    </div>
    <div>
      <h1>已完成</h1>
      <div class="mdui-panel" mdui-panel>
          <div class="mdui-panel-item" style="margin: 15px 20px 15px 20px;border-radius: 10px;" v-for="(item,i) in aria2process" v-if="!completeOrDownload(item.Status)" :key='i'>
              <div class="mdui-panel-item-header" style="height: 70px;">
                <div class="mdui-panel-item-title" style="width: 80%;">{{item.Name}}</div>
                <i class="mdui-panel-item-arrow mdui-icon material-icons">keyboard_arrow_down</i>
              </div>
              <div class="mdui-panel-item-body">
                <p v-for="(item_,index_) in item.Files" :key="index_">
                  {{item_.path}}
                </p>
                <div class="mdui-panel-item-actions">
                  <button class="mdui-btn mdui-ripple" style="border-radius: 10px;" @click="taskcancel(item)">移除</button>
                </div>
              </div>
          </div>
        </div>
    </div>
    <div class="mdui-dialog" id="linkdialog" style="border-radius: 10px;">
        <div class="mdui-dialog-title">下载链接</div>
        <div class="mdui-textfield mdui-textfield-floating-label mdui-center" style="width: 400px;">
          <label class="mdui-textfield-label">磁力/HTTP链接</label>
          <textarea class="mdui-textfield-input" v-model="link" rows="3"></textarea>
        </div>
        <div class="mdui-dialog-actions">
          <button class="mdui-btn mdui-ripple" mdui-dialog= "{target:'#downpathdialog'}" @click="downButton1" mdui-dialog-confirm>下载</button>
          <button class="mdui-btn mdui-ripple mdui-dialog-cancel" mdui-dialog-cancel>取消</button>
        </div>
    </div>
    <div class="mdui-dialog" id="downpathdialog" style="border-radius: 10px;">
        <div class="mdui-dialog-title mdui-text-truncate">下载至 {{downpath}}</div>
        <div class="mdui-dialog-content mdui-center" style="min-height: 300px;">
          <div class="mdui-row">
            <div class="mdui-col-xs-6 mdui-col-md-4" v-for="(item,i) in downdirlist" :key="i">
              <button style="margin: 5px;width: 150px;" class="mdui-btn mdui-color-grey-200 mdui-ripple mdui-text-truncate" @click="downButton2(item)">{{item}}</button>
            </div>
          </div>
        </div>
        <div class="mdui-dialog-actions">
          <button class="mdui-btn mdui-ripple" @click="downfile" mdui-dialog-confirm>开始下载</button>
          <button class="mdui-btn mdui-ripple mdui-dialog-cancel" mdui-dialog-cancel>取消</button>
        </div>
    </div>
    <button class="mdui-fab mdui-ripple mdui-fab-fixed mdui-color-amber-700" mdui-dialog= "{target:'#linkdialog'}" ><i class="mdui-icon material-icons">add</i></button>
  </div>
</template>

<script>
  export default{
    data(){
      return{
        link:'',
        downpath:'',
        downdirlist:[],
        timer: 10,
        aria2process: [],
        aria2changestatus: {
          gid: '',
          type: ''
        }
      }
    },
    methods:{
      completeOrDownload(status){
        if(status == 'complete' || status == 'removed' || status == 'error'){
          return false
        }else{
          return true
        }
      },
      refresh(){
        this.aria2status()
      },
      taskcancel: function (item) {
        this.aria2changestatus.gid = item.Gid
        if (item.Status === 'active') {
          this.aria2changestatus.type = 2
        } else if (item.Status === 'removed') {
          this.aria2changestatus.type = 4
        } else {
          this.aria2changestatus.type = 5
        }
        this.$axios({
          method: 'PUT',
          url: '/aria2',
          data: this.aria2changestatus
        }).then((res) => {
          var tmp = res.data
          if (tmp === 'succeed') {
            if(this.aria2changestatus.type == 2){
              mdui.snackbar({
                message: '任务取消成功',
                position: 'top'
              })
            }else{
              mdui.snackbar({
                message: '任务移除成功',
                position: 'top'
              })
            }
          } else {
            mdui.snackbar({
               message: '任务取消失败',
               position: 'top'
             })
          }
        })
      },
      taskprocess: function (item) {
        this.aria2changestatus.gid = item.Gid
        if (item.Status === 'active') {
          this.aria2changestatus.type = 1
        } else if (item.Status === 'paused') {
          this.aria2changestatus.type = 3
        }
        this.$axios({
          method: 'PUT',
          url: '/aria2',
          data: this.aria2changestatus
        }).then((res) => {
          var tmp = res.data
          if (tmp === 'succeed') {
            if (item.Status === 'active') {
              mdui.snackbar({
                  message: '任务暂停',
                  position: 'top'
                })
            } else {
              mdui.snackbar({
                  message: '任务继续',
                  position: 'top'
                })
            }
          } else {
            mdui.snackbar({
                message: '暂停失败',
                position: 'top'
              })
          }
        })
      },
      downfile(){
        let tmp = {
          url: this.link.split('\n'),
          path: this.downpath
        }
        this.$axios({
          url: '/aria2',
          method: 'POST',
          data: tmp
        }).then((res) => {
          tmp = res.data
          if (tmp > 0) {
            mdui.snackbar({
                message: '添加成功' + tmp + '个',
                position: 'top'
              })
          } else {
            mdui.snackbar({
                message: '失败',
                position: 'top'
              })
          }
        })
      },
      downButton1(){
        this.downdirlist = []
        this.downpath = '/'
        let par = encodeURIComponent(this.downpath)
        this.$axios.get('/file/' + par).then(
          response => {
            if (response.data !== null) {
              for (var item of response.data) {
                if (item['DataType'] === 'dir') {
                  this.downdirlist.push(item.DataName)
                }
              }
            }
          }
        )
      },
      downButton2(name){
        this.downdirlist = []
        if (this.downpath == '/'){
          this.downpath += name
        }else{
          this.downpath += '/'+name
        }
        let par = encodeURIComponent(this.downpath)
        this.$axios.get('/file/' + par).then(
          response => {
            if (response.data !== null) {
              for (var item of response.data) {
                if (item['DataType'] === 'dir') {
                  this.downdirlist.push(item.DataName)
                }
              }
            }
          }
        )
      },
      size: function (values) {
        var unit = ['B', 'KB', 'MB', 'GB', 'TB']
        var i = 0
        let value = parseFloat(values)
        while (value >= 1024) {
          value = value / 1024
          i++
        }
        let tmp = value.toFixed(2) + unit[i]
        return (tmp)
      },
      getpercentnge: function (CompletedLength, TotalLength) {
        if (TotalLength === '0') {
          return 0
        }
        let tmp = Number((CompletedLength / TotalLength * 100).toFixed(2))
        return tmp
      },
      downspeed: function (values) {
        var unit = ['B/s', 'KB/s', 'MB/s', 'GB/s', 'TB/s']
        var i = 0
        let value = parseFloat(values)
        while (value >= 1024) {
          value = value / 1024
          i++
        }
        let tmp = value.toFixed(2) + unit[i]
        return (tmp)
      },
      aria2status: function () {
        this.$axios({
          url: '/aria2'
        }).then((res) => {
          this.aria2process = res.data
        })
        mdui.mutation()
      }
    },
    beforeMount() {
      this.$axios.get('/aria2test').then(res => {
        if (res.data !== 'failed') {
          this.aria2status()
          this.timer = setInterval(() => {
            this.aria2status()
          }, res.data.Time * 1000)
        } else {
          mdui.snackbar({
              message: '未开启Aria2',
              position: 'top'
            })
        }
      })
    },
    destroyed() {
      clearInterval(this.timer)
    }
  }
</script>

<style>
</style>



