<template>
  <div class="mdui-valign" style="height: 500px;">
    <div class="mdui-center" style="width: 400px">
      <div class="mdui-textfield mdui-textfield-floating-label">
          <i class="mdui-icon material-icons">email</i>
          <label class="mdui-textfield-label">Email</label>
          <input class="mdui-textfield-input" v-model="email" type="email" required/>
          <div class="mdui-textfield-error">邮箱格式错误</div>
        </div>
        <div class="mdui-textfield mdui-textfield-floating-label">
          <i class="mdui-icon material-icons">vpn_key</i>
          <label class="mdui-textfield-label">Password</label>
          <input class="mdui-textfield-input" v-model="password" type="text" pattern="^.*(?=.{6,}).*$" required/>
          <div class="mdui-textfield-error">密码至少 6 位</div>
        </div>
        <div class="mdui-center" style="width: 200px">
          <button class="mdui-btn mdui-color-pink-400 mdui-ripple"@click="userlogin">登录</button>
          <button class="mdui-btn mdui-color-pink-400 mdui-ripple"@click="userregister()">注册</button>
        </div>
    </div>
  </div>
</template>

<script>
export default{
  data(){
    return{
      email:'admin@godcloud.com',
      password:'123456'
    }
  },
  methods:{
    userlogin(){
      let params = new FormData()
      params.append('email', this.email)
      params.append('pw', this.password)
      params.append('checked', true)
      this.$axios({
        method: 'POST',
        url: '/login',
        data: params,
        headers: {
          'Content-Type': 'multipart/form-data'
        }
      }).then(
        response => {
          if (response.data !== 'failed') {
            mdui.snackbar({
                message: '登录成功',
                position: 'top'
              })
            this.$router.push({path: '/home'})
          }else{
            mdui.snackbar({
                message: '登录失败',
                position: 'top'
              })
          }
        }
      )
    },
    userregister(){
      mdui.snackbar({
          message: '未开放',
          position: 'top'
        })
    }
  },
  beforeCreate() {
    if (this.$cookies.get('GODKEY') != null){
      this.$router.push({path: '/home'})
    }
  }
}
</script>

<style>
</style>



