<template>
  <div>
    <div class="mdui-center" style="width: 300px;margin-top: 150px;">
      <div style="text-align: center;">
        <label class="mdui-switch">
        开 关
        <input type="checkbox" v-model="value2" />
        <i class="mdui-switch-icon"></i>
        </label>
      </div>
      <div class="mdui-textfield mdui-textfield-floating-label mdui-textfield-not-empty">
        <label class="mdui-textfield-label">本地RPC服务器端口</label>
        <textarea class="mdui-textfield-input" v-model="port"></textarea>
      </div>
      <div class="mdui-textfield mdui-textfield-floating-label mdui-textfield-not-empty">
        <label class="mdui-textfield-label">RPC Secret</label>
        <textarea class="mdui-textfield-input" v-model="secret"></textarea>
      </div>
      <div class="mdui-textfield mdui-textfield-floating-label mdui-textfield-not-empty">
        <label class="mdui-textfield-label" >刷新间隔(秒)</label>
        <textarea class="mdui-textfield-input" v-model="time"></textarea>
      </div>
      <div class="mdui-textfield mdui-textfield-floating-label mdui-textfield-not-empty">
        <label class="mdui-textfield-label">临时目录(绝对路径)</label>
        <textarea class="mdui-textfield-input" v-model="tmpdownpath"></textarea>
      </div>
      <div style="text-align: center;margin-top: 30px;">
        <button class="mdui-btn mdui-ripple mdui-color-pink-200" @click="save" style="border-radius: 10px;margin-right: 20px;">保存</button>
        <button class="mdui-btn mdui-ripple mdui-color-pink-200" @click="testconnect" style="border-radius: 10px;margin-left: 20px;">测试</button>
      </div>
    </div>
  </div>
</template>

<script>
  export default{
    data () {
      return {
        value2: false,
        port: '',
        tmpdownpath: '',
        secret: '',
        time: ''
      }
    },
    methods: {
      save () {
        var pp = new FormData()
        pp.append('Status', this.value2 === false ? 'no' : 'yes')
        pp.append('Port', this.port)
        pp.append('TmpDownPath', this.tmpdownpath)
        pp.append('Secret', this.secret)
        pp.append('Time', this.time)
        this.$axios({
          method: 'POST',
          url: '/aria2config',
          headers: {
            'Content-Type': 'multipart/form-data'
          },
          data: pp
        }).then(
          res => {
            if (res.status === 200) {
              mdui.snackbar({
                  message: '保存成功',
                  position: 'top'
                })
            } else {
              mdui.snackbar({
                  message: '保存失败',
                  position: 'top'
                })
            }
          }
        )
      },
      testconnect () {
        this.$axios.get('/aria2test').then(res => {
          if (res.data !== 'failed') {
            mdui.snackbar({
                message: '连接成功，aria2版本：' + res.data.Version,
                position: 'top'
              })
          } else {
            mdui.snackbar({
                message: '连接失败',
                position: 'top'
              })
          }
        })
      }
    },
    beforeMount () {
      this.$axios.get('/aria2config').then(res => {
        this.value2 = res.data.Status === 'yes'
        this.port = res.data.Port
        this.time = res.data.Time
        this.secret = res.data.Secret
        this.tmpdownpath = res.data.TmpDownPath
      })
    }
  }
</script>

<style>
</style>



